(function() {
  "use strict";
  let search = document.getElementById("search");
  let searchInput = document.getElementById("searchInput");
  let searchBlock = document.getElementById("searchBlock");
  let subscribe = document.getElementById("subscribe");
  let hidden = document.querySelectorAll(".login__btn--hide");
  let searchBtn = document.getElementById("searchBtn");

  document.addEventListener("click", function(event) {
    if (window.innerWidth < 993) {
      searchInput.style.width = "70%";
      searchBlock.style.width = "310px";
      searchInput.style.opacity = 1;
      searchBtn.style.display = "block";
      hidden.forEach(el => {
        el.style.display = "none";
      });
    } else {
      searchInput.style.opacity = 1;
      searchInput.style.width = "70%";
      searchBlock.style.width = "310px";
    }

    if (event.target.closest("#searchBlock")) return;

    if (window.innerWidth < 993) {
      searchBtn.style.display = "none";
      searchInput.style.opacity = 0;
      searchInput.style.width = "0";
      searchBlock.style.width = "80px";
      hidden.forEach(el => {
        el.style.display = "block";
      });
    } else {
      searchInput.style.opacity = 0;
      searchInput.style.width = "0";
      searchBlock.style.width = "143px";
    }
  });

  let navBar = document.getElementById("navBar");

  window.addEventListener("scroll", function() {
    if (window.innerWidth < 993) {
      navBar.style.backgroundColor = "rgba(110, 119, 74, 0.9)";
    } else {
      if (pageYOffset >= 200) {
        navBar.style.backgroundColor = "rgba(110, 119, 74, 0.9)";
      } else {
        navBar.style.backgroundColor = "transparent";
      }
    }
  });

  let hamburgerIcon = document.getElementById("hamburger-icon");
  let links = document.querySelectorAll(".nav-link");

  links.forEach(el =>
    el.addEventListener("click", function() {
      hamburgerIcon.classList.remove("active");
      navBar.style.top = "-50%";
    })
  );

  hamburgerIcon.addEventListener("click", function() {
    if (hamburgerIcon.classList == "active") {
      hamburgerIcon.classList.remove("active");
      navBar.style.top = "-50%";
    } else {
      hamburgerIcon.classList.add("active");
      navBar.style.top = 0;
    }
  });
})();

var mySwiper = new Swiper(".swiper-container", {
  speed: 600,
  spaceBetween: 30,
  slidesPerView: 1,
  loop: true,
  breakpoints: {
    640: {
      slidesPerView: 2,
      spaceBetween: 20
    },

    1000: {
      slidesPerView: 3,
      spaceBetween: 30
    }
  },
  navigation: {
    nextEl: ".slider__arrow-next",
    prevEl: ".slider__arrow-prev"
  }
});

var mySwiper = new Swiper(".big-slider", {
  speed: 1000,
  loop: true,
  navigation: {
    nextEl: ".big-slider__arrow-next",
    prevEl: ".big-slider__arrow-prev"
  }
});